
.markdown{max-width:50em
}
.markdown__btn{float:right;margin-top:1em
}
.markdown pre{background-color:#011627;padding:1rem;text-align:left;color:#f8f6f6;font-size:1.3em;border:none
}
.markdown table{border:1px solid #eee
}
.markdown table td{padding:1em
}
.markdown table thead{background-color:#eee
}
.markdown table thead th{padding:.5em
}
.markdown blockquote{margin:0;border-left:5px solid #7a7a7a;font-style:italic;padding:1.33em;text-align:left
}
.markdown ul,.markdown ol,.markdown li{text-align:left
}
.markdown p{font-size:1.3em
}
.markdown h2{margin-top:2em;padding-top:1em;border-top:1px solid #eee
}
.markdown h1{margin-top:2em;text-align:center
}
.markdown img{max-width:100%
}



