
@media print{
*,*:before,*:after{background:transparent !important;color:#000 !important;box-shadow:none !important;text-shadow:none !important
}
a,a:visited{text-decoration:underline
}
a[href]:after{content:" (" attr(href) ")"
}
abbr[title]:after{content:" (" attr(title) ")"
}
a[href^="#"]:after,a[href^="javascript:"]:after{content:""
}
pre,blockquote{border:1px solid #999;page-break-inside:avoid
}
thead{display:table-header-group
}
tr,img{page-break-inside:avoid
}
img{max-width:100% !important
}
p,h2,h3{orphans:3;widows:3
}
h2,h3{page-break-after:avoid
}
.no-print,.no-print *{display:none !important
}
.print-only{display:block !important
}
.markdown h1,.markdown h2,.markdown h3{margin:1em 0 !important
}
}
body,html{font-size:1rem;font-family:"Source Sans Pro",sans-serif;background-color:#011627
}
.main{padding-top:3em;background-color:#fff
}
.highlight{background-color:#4e4e4e;color:#fff;display:inline-block;padding:.3em;border-radius:8px
}
.highlight .icon{width:1em;height:1em;display:inline-block;vertical-align:middle;fill:#fff;margin:0 .5em
}
.highlight--alert{background-color:#17d7f7
}
.highlight--alt{background-color:#011627
}
.highlight--num{width:2em;height:2em;border-radius:50%;text-align:center;margin-right:.4em
}
.highlight--mute{border:2px solid #EAF6FF;background-color:transparent;color:#232528;margin-right:1em
}
.tag{background-color:#4e4e4e;border-radius:8px;color:#fff;display:block;height:26px;line-height:26px;padding:0 20px 0 23px;position:relative;margin:0 10px 10px 0;text-decoration:none;-webkit-transition:color 0.2s
}
.tag:hover{color:#fff;text-decoration:none;background-color:#6f6f6f
}
.tag:before{background:#fff;border-radius:10px;box-shadow:inset 0 1px rgba(0,0,0,0.25);content:'';height:6px;left:10px;position:absolute;width:6px;top:10px
}
.current .tag,.tag.current{background-color:#17d7f7;color:#232528
}
.current .tag:after,.tag.current:after{border-left:10px solid #17d7f7
}
@media only screen and (min-width: 992px){
.tag{display:inline-block
}
}
.btn{background-color:#4e4e4e;border:none;border-radius:0;font-family:"Montserrat",sans-serif;font-weight:bold
}
.btn-primary{transition:all .3s;background-color:#17d7f7;color:#232528;text-transform:uppercase;font-weight:bold;letter-spacing:.1em
}
.btn-primary:hover,.btn-primary:focus,.btn-primary:active{color:#232528;background-color:#48e0f9
}
p{line-height:2rem;margin-top:1.4rem;margin-bottom:1.4rem;padding-right:0;vertical-align:baseline
}
p,ol,ul{font-size:1.2rem
}
a{color:#011627
}
.print-only{display:none
}
.site-wrap__navbar{background-color:#011627;color:#fff;margin-bottom:0;border-bottom:none
}
.site-wrap__navbar a,.site-wrap__navbar .navbar-brand{color:#fff
}
.site-wrap__navbar a:hover,.site-wrap__navbar .navbar-brand:hover{color:#17d7f7
}
.site-wrap__navbar .navbar-brand{font-weight:bold
}
.site-wrap__menu{background-color:#011627;color:#fff;transition:all .3s;width:100%;height:100%;position:fixed;left:100%;top:0;overflow:hidden
}
.site-wrap.menuOpen .site-wrap__menu{left:0
}
.logo{fill:#17d7f7;width:7em;height:3em;position:absolute;left:0;top:.3em
}
.footer{position:relative;color:#fff;padding:1em 0;font-size:.8em;text-align:center;z-index:100;background-color:#011627
}
.footer a{color:#fff
}
.footer__list{margin:1em 0;list-style:none;padding:0
}
.footer__list a{display:inline-block;position:relative
}
.footer__list a .c-tooltip{position:absolute;bottom:100%;left:-1em;background-color:#17d7f7;color:#011627;display:inline-block;padding:.3em;border-radius:3px;opacity:0;visibility:hidden;text-align:left;white-space:nowrap
}
.footer__list a:hover .c-tooltip{opacity:1;visibility:visible
}
.footer__list .icon{fill:#fff;width:2em;height:2em
}
.footer__list .icon--small{width:1em;height:1em
}
.footer__copy a{text-decoration:underline
}
@media only screen and (min-width: 768px){
.footer{text-align:left
}
.footer__inner{display:-ms-grid;display:grid;-ms-grid-columns:50% 50%;grid-template-columns:50% 50%;-ms-flex-align:center;align-items:center
}
.footer__list{margin:0;text-align:right
}
.footer__list li{display:inline-block;vertical-align:middle;margin:0 .3em
}
}
.content{padding-top:2em;padding-bottom:2em
}
.push-menu{padding-top:5em;font-size:1rem;text-align:center
}
.push-menu__item{padding:0;font-size:2em
}
.push-menu__item a{color:#17d7f7;padding:1em;border-bottom:2px solid #022440
}
.navbar-collapse{text-align:right;position:relative
}
.main-nav{text-align:center;float:none;display:inline-block;font-size:1em
}
.main-nav li a.main-nav__link{color:#fff
}
.main-nav li a.main-nav__link.router-link-active{color:#17d7f7
}
.main-nav li a.main-nav__link:hover{color:#17d7f7
}
.navbar-default .navbar-toggle,.navbar-default .navbar-toggle:focus,.navbar-default .navbar-toggle:hover{background:none;border:none;padding:0;width:2em;height:2em;position:relative
}
.navbar-toggle .icon{transition:all .3s;width:100%;height:100%;left:0;top:0;position:absolute;fill:#17d7f7
}
.navbar-toggle .icon-close{opacity:0;visibility:0
}
.menuOpen .navbar-toggle .icon{opacity:0;visibility:hidden
}
.menuOpen .navbar-toggle .icon-close{opacity:1;visibility:visible
}
.page-title{padding:3em 0;margin:0;color:#17d7f7;position:relative;background-position:center center;background-size:cover;background-attachment:fixed;text-align:center;font-weight:300
}
.page-title span{position:relative
}
.page-title__sub{display:block;font-size:.5em;margin-top:.5em;text-transform:uppercase;color:#fff;font-weight:bold;letter-spacing:.05em
}
.page-title:before{content:'';position:absolute;left:0;top:0;width:100%;height:100%;background-color:rgba(1,22,39,0.8)
}
.home .page-title{background-image:url("./assets/code.jpg")
}
.outline .page-title{background-image:url("./assets/keys.jpg")
}
.assignments .page-title{background-image:url("./assets/work.jpg")
}
.tutorial .page-title{background-image:url("./assets/study.jpg")
}
.students .page-title{background-image:url("./assets/office.jpg")
}
.page-heading{font-family:"Montserrat",sans-serif;font-size:.8em;letter-spacing:.05em;margin-top:3em
}
.timeline{position:relative;padding:1em 0;list-style:none;margin:0
}
.timeline:before{content:'';width:.5em;height:100%;left:0;top:0;position:absolute;background-color:#EAF6FF
}
.timeline__point{position:relative;padding-left:1.8em
}
.timeline__point:before{content:'';width:1em;height:1em;margin-left:-.2em;left:0;top:45%;position:absolute;background-color:#4e4e4e;border-radius:50%
}
.timeline__point.current:before{background-color:#17d7f7
}
.day{box-shadow:0 0 2px rgba(35,37,40,0.3);padding:4em 1em 4em 1em;position:relative;margin:0 0 3em
}
.day__item{margin-bottom:.5em;display:block
}
.day__label{background:#EAF6FF;font-weight:bold;font-size:1em;display:block;padding:.7em 1em .5em;margin:1em 0;text-align:center
}
.day__label--alert{font-size:.9em;background-color:#7ae8fa;border-color:#17d7f7;color:#011627
}
.day__label--alt{background-color:#011627;color:#fff
}
.day__date{display:block;font-weight:bold;text-align:center;margin-bottom:1em;font-size:1.5em
}
.day__list{padding:0;list-style:none
}
.day__due{padding:0;margin:2em 0 0;border:3px solid #17d7f7;text-align:center
}
.day__alert{margin:0 0 .3em;border-radius:0;display:block;font-size:1.5em
}
.day__slides{position:absolute;right:.5em;bottom:.5em;color:#4e4e4e
}
.day__slides:hover{text-decoration:none;color:#17d7f7
}
.day__slides .icon{width:2em;height:2em;fill:#4e4e4e
}
.day__slides .icon,.day__slides span{display:inline-block;vertical-align:middle
}
@media only screen and (min-width: 768px){
.day__student{position:absolute;top:0;left:0
}
}
@media only screen and (min-width: 992px){
.day{display:-ms-flexbox;display:flex;-ms-flex-pack:start;justify-content:flex-start;-ms-flex-wrap:wrap;flex-wrap:wrap
}
.day__block{padding:0 .5em;width:30%
}
.day__block--main{width:70%;padding-left:2em
}
.day__block--full{width:100%
}
.day__date{text-align:left;margin-bottom:.5em
}
.day__item{padding:0;display:block;margin-bottom:1.5em
}
.day__student{position:relative;margin-bottom:3em
}
.day__label{margin:0 0 1em;text-align:left;font-size:1em
}
}
.grid{margin-top:2em
}
.grid__block{box-shadow:0 0 3px rgba(35,37,40,0.3)
}
@media only screen and (min-width: 992px){
.grid{display:-ms-grid;display:grid;-ms-grid-columns:1fr 1fr;grid-template-columns:1fr 1fr;grid-column-gap:2em;grid-row-gap:2em
}
.grid__block{position:relative
}
}
.text-grid{margin-bottom:2em
}
.text-grid__block{margin-bottom:.5em
}
.text-grid__item:first-child{font-weight:bold
}
@media only screen and (min-width: 992px){
.text-grid{display:-ms-flexbox;display:flex
}
.text-grid__block{padding-right:2em
}
}
.project{display:block;margin-bottom:2em
}
.project__block{display:block
}
.project a{text-decoration:none
}
.project__title{transition:all .3s;font-size:1.4em;display:block;background-color:#4e4e4e;color:#fff;padding:.5em
}
.project__title:hover{color:#fff;background-color:#5b5b5b
}
.project__weight{text-align:center;background-color:#EAF6FF;font-weight:700;display:block;padding:.4em 0
}
.project__headline{display:block
}
.project__subheadline{font-size:.8em;text-transform:uppercase
}
.project__desc,.project__skills{padding:.5em 1em;display:block
}
.project__skill{margin-right:.5em;margin-bottom:.5em;display:inline-block
}
.project__cta{display:block
}
.project__list{list-style:none;padding:0
}
.project__list li{margin-bottom:1em
}
.project__list--numbered{padding:0
}
.project__list--numbered li{counter-increment:step-counter;position:relative;display:block;padding-left:1.2em
}
.project__list--numbered li:before{content:counter(step-counter);margin-right:5px;position:absolute;left:0;top:50%;transform:translateY(-50%);font-size:.7em;color:#17d7f7;font-weight:bold;font-family:"Montserrat",sans-serif
}
@media only screen and (min-width: 992px){
.project{padding-bottom:1em
}
.project__title{padding:.8em .5em
}
.project__weight{background-color:#353535;color:#fff;padding:1em;top:0;right:0;position:absolute;border-radius:0 0 0 8px
}
.project__cta{position:absolute;bottom:0;left:0;width:100%
}
}
.tutorial__list{list-style:none;padding:0
}
.tutorial__item{margin-bottom:2em;padding:0
}
.tutorial__title{background-color:#4e4e4e;color:#fff;margin:0;padding:1em
}
.syllabus p{line-height:2
}
.date-nav{padding-top:2em;list-style:none
}
.date-nav__item{display:inline-block;cursor:pointer
}
.date-nav__item a:focus{color:#fff;text-decoration:none
}
.breadcrumbs{background-color:#011627;color:#fff;padding:1em 0
}
.breadcrumbs a{color:#fff
}
.breadcrumbs .current{color:#17d7f7
}
.print-btn{background-color:transparent;border:none;outline:none
}
.print-btn:focus .icon,.print-btn:active .icon{fill:#034072
}
.print-btn .icon{width:2em;height:2em;fill:#011627
}
.back-top{transition:opacity .3s;position:fixed;bottom:1em;right:1em;opacity:.3;z-index:10
}
.back-top:hover{opacity:1
}



